// Just an example
.navbar{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100vw;
  padding: toRem(32) 0;
  transition: all linear .2s;
  z-index: 100;
  background: transparent;
  @include media-breakpoint-down(md) {
    padding: toRem(20) 0;
  }
  &.scrolled{
    background-color: $color-primary;
  }
  &.active{
    background-color: $color-white;
    box-shadow: 0px 78px 200px -50px rgba($color-super-black, 0.17);
    backdrop-filter: blur(25px);
    padding-bottom: toRem(32);
    .navbar__title-text{
      transition: all linear .2s;
      path{
        transition: all linear .2s;
        fill: $color-black;
      }
    }
    .btn-purchase{
      background: $color-white !important;
      border: 1px solid $color-primary !important;
      color: $color-primary !important;
      &:hover{
        background: $color-primary !important;
        border: 1px solid $color-primary !important;
        color: $color-white !important;
      }
    }
  }
  .navbar__title{
    .navbar__title-logo{
      display: flex;
      align-items: center;
      gap: toRem(10);
      transition: all linear .2s;
      @include media-breakpoint-down(md) {
        gap: toRem(8);
      }
      @include media-breakpoint-down(xs-2) {
        max-width: toRem(106);
      }
      .navbar__title-icon{
        transition: all linear .2s;
        @include media-breakpoint-down(md) {
          width: toRem(32);
          height: toRem(32);
        }
      }
    }
  }
  .navbar__links{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: toRem(49);
    transition: all linear .2s;
    @include media-breakpoint-down(xl) {
      gap: toRem(40);
    }
    @include media-breakpoint-down(lg-2) {
      gap: toRem(32);
    }
    @include media-breakpoint-down(lg) {
      gap: toRem(16);
    }
    @include media-breakpoint-down(md) {
      display: none;
    }
    .navbar__links__link{
      font-size: toRem(14);
      line-height: 1.4;
      transition: all linear .2s;
      color: $color-grey-5;
      &:hover{
        color: $color-tertiary;
      }
    }
  }
  .navbar__sign{
    display: flex;
    gap: toRem(16);
    transition: all linear .2s;
    align-items: center;
    @include media-breakpoint-down(xs-2) {
      gap: toRem(4);
    }
    .navbar__menu{
      transition: all linear .2s;
      display: none;
      cursor: pointer;
      @include media-breakpoint-down(md) {
        display: block;
      }
    }
  }
  .navbar-mobile{
    position: relative;
    width: 100vw;
    display: none;
    transition: all linear .2s;
    &.active{
      display: flex;
      flex-direction: column;
      transition: all linear .2s;
    }
    .navbar__links{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      width: 100%;
      padding-top: toRem(20);
      transition: all linear .2s;
      .navbar__links__link{
        width: 100%;
        display: flex;
        padding: toRem(24) toRem(20);
        font-size: toRem(18);
        line-height: 1.35;
        font-weight: 500;
        color: $color-black;
        transition: all linear .2s;
        border-bottom: 1px solid $color-grey-5;
        &:hover{
          color: $color-primary;
        }
      }
    }
  }
}