//Line
.line{
  .line__content{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: toRem(32);
    .line__border{
      display: block;
      width: 100%;
      max-width: toRem(456);
      height: 1px;
      opacity: .2;
      background-color: $color-primary;
    }
  }
}
//cta
.cta{
  position: relative;
  padding: toRem(191) 0 toRem(80);
  @include media-breakpoint-down(lg) {    
    padding: toRem(64) 0;
  }
  .container{
    @include media-breakpoint-down(xs-2) {
      padding: 0 !important;
    }
  }
  .cta__main{
    position: relative;
    padding: toRem(57) 0 toRem(78) toRem(72);
    border-radius: toRem(24);
    background: $color-primary;
    box-shadow: toRem(0) toRem(64) toRem(200) toRem(-50) rgba(22, 10, 111, 0.05);
    width: 100%;
    max-width: toRem(1024);
    margin: 0 auto;
    z-index: 1;
    @include media-breakpoint-down(xl) {
      padding: toRem(57) 0 toRem(78) toRem(48);
    }
    @include media-breakpoint-down(lg) {
      padding: toRem(57) 0 toRem(78) toRem(32);
    }
    @include media-breakpoint-down(md) {
      background-position: bottom bottom;
      padding: toRem(40) toRem(14) toRem(0);
    }
    @include media-breakpoint-down(xs-2) {
      border-radius: 0;
    }
    .cta__content{
      display: flex;
      flex-direction: column;
      gap: toRem(24);
      max-width: toRem(283);
      z-index: 2;
      @include media-breakpoint-down(md) {
        align-items: center;
        text-align: center;
        justify-content: center;
        max-width: 100%;
      }
    }
    .cta__desc{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: toRem(5);
      @include media-breakpoint-down(md) {
        justify-content: center;
      }
    }
    .cta__desc-device{
      width: max-content;
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: toRem(2) toRem(8);
      gap: toRem(4);

      background: rgba($color-white, 0.3);
      border-radius: toRem(40);
    }
    .btn-cta{
      margin-top: toRem(16);
    }
    .cta__illustration{
      position: absolute;
      top: toRem(-170);
      right: toRem(0);
      z-index: 2;
      @include media-breakpoint-down(xl) {
        right: toRem(-32);
      }
      @include media-breakpoint-down(lg) {
        top: toRem(16);
        right: toRem(0);
        max-width: toRem(380);
      }
      @include media-breakpoint-down(md) {
        position: relative;
        max-width: unset;
        width: 100%;
        max-width: toRem(474);
        margin: 0 auto;
        display: block;
        top: unset;right: unset;
      }
    }
    .cta__bg{
      position: absolute;
      left: 0;top: 0;right: 0;bottom: 0;
      object-fit: cover;
      z-index: 0;
      @include media-breakpoint-down(md) {
        top: unset;
        left: unset;
        object-fit: unset;
        width: toRem(604);
        right: toRem(-12);
        bottom: 0;
        height: toRem(311);
      }
    }
  }
}
// Footer
.footer{
  padding: toRem(32) 0;
  .footer-main{
    display: flex;
    flex-direction: column;
    gap: toRem(32);
    align-items: center;
    justify-content: center;
    .footer-main__logo{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: toRem(15);
      .footer-main__logo-src{
        width: toRem(60);
        height: toRem(60);
      }
    }
    .footer-main__desc{
      color: $color-black;
    }
  }
  .footer-copyright{
    margin-top: toRem(72);
  }
}